import { IndividualItemMain } from './individual-item';
import { Link } from "react-router-dom";

//Cdigo do item nico do portiflio que receber por parmetro 
//a lista de itens da coleo 'Portifolio-item'

export const PortifolioItemMain = ({ items }) => {
    return (
        items.map((individualItem) => { //Map pela lista recebida por parmetro
            if (individualItem.checkType === 'img1') { //Verificando se o tipo de pgina de descrio  o 1
                return (
                    <Link to={"/portfolio-details-1/" + individualItem.ID} key={individualItem.ID}>
                        <IndividualItemMain key={individualItem.ID} individualItemMain={individualItem} />
                    </Link>
                )
            } else if (individualItem.checkType === 'img2') { //Verificando se o tipo de pgina de descrio  o 2
                return (
                    <Link to={"/portfolio-details/" + individualItem.ID} key={individualItem.ID}>
                        <IndividualItemMain key={individualItem.ID} individualItemMain={individualItem} />
                    </Link>
                )
            } else if (individualItem.checkType === 'img3') { //Verificando se o tipo de pgina de descrio  o 3
                return (
                    <Link to={"/portfolio-details-2/" + individualItem.ID} key={individualItem.ID}>
                        <IndividualItemMain key={individualItem.ID} individualItemMain={individualItem} />
                    </Link>
                )
            }
        }
        ))
};